<template>
    <div class="login-container">
        <el-form
            ref="loginForm"
            :model="loginForm"
            :rules="loginRules"
            class="login-form"
            auto-complete="on"
            label-position="left"
        >
            <div class="title-container">
                <h3 class="title">主音</h3>
            </div>

            <el-form-item prop="user">
                <span class="svg-container">
                    <svg-icon icon-class="user" />
                </span>
                <el-input
                    ref="username"
                    v-model="loginForm.identifier"
                    placeholder="Username"
                    name="username"
                    type="text"
                    tabindex="1"
                    auto-complete="on"
                />
            </el-form-item>

            <el-form-item prop="password">
                <span class="svg-container">
                    <svg-icon icon-class="password" />
                </span>
                <el-input
                    :key="passwordType"
                    ref="password"
                    v-model="loginForm.credential"
                    :type="passwordType"
                    placeholder="Password"
                    name="password"
                    tabindex="2"
                    auto-complete="on"
                />
                <span class="show-pwd" @click="showPwd">
                    <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
                </span>
            </el-form-item>

            <!-- <el-form-item prop="password">
                <span class="svg-container">
                    <svg-icon icon-class="password" />
                </span>
                <el-input
                    ref="authcode"
                    v-model="loginForm.authcode"
                    placeholder="验证码"
                    name="authcode"
                    tabindex="3"
                    @keyup.enter.native="handleLogin"
                />
                <span class="authcode">
                    <img :src="authCodeSrc" @click="changeAuthCode" style="height: 100%" />
                </span>
            </el-form-item>-->

            <el-button
                :loading="loading"
                type="primary"
                style="width:100%;margin-bottom:30px;"
                @click.native.prevent="handleLogin"
            >登录</el-button>

            <!-- <div class="tips">
        <span style="margin-right:20px;">username: admin</span>
        <span> password: any</span>
            </div>-->
        </el-form>
        <footer :style="footerStyle">
            2020©️主音
            <br />All rights reserved.
        </footer>
    </div>
</template>

<script>
import { validUsername } from '@/utils/validate'
import { getToken } from '@/utils/auth'

export default {
    name: 'Login',
    beforeCreate() {
        if (getToken()) {
            this.$router.push({
                path: '/'
            })
        }
    },
    data() {
        const validateUsername = (rule, value, callback) => {
            if (!validUsername(value)) {
                callback(new Error('Please enter the correct user name'))
            } else {
                callback()
            }
        }
        const validatePassword = (rule, value, callback) => {
            if (value.length < 6) {
                callback(
                    new Error('The password can not be less than 6 digits')
                )
            } else {
                callback()
            }
        }
        return {
            loginForm: {
                identityType: 'username',
                identifier: 'admin',
                credential: 'hen8888..',
                authcode: undefined
            },
            loginRules: {
                identifier: [
                    {
                        required: true,
                        trigger: 'blur',
                        validator: validateUsername
                    }
                ],
                credential: [
                    {
                        required: true,
                        trigger: 'blur',
                        validator: validatePassword
                    }
                ]
            },
            loading: false,
            passwordType: 'password',
            redirect: undefined,
            authCodeSrc: this.$conf.host + 'authcode',
            footerStyle: {
                position: 'absolute',
                width: '100%',
                textAlign: 'center',
                bottom: '0px',
                fontSize: '10px'
            }
        }
    },
    watch: {
        $route: {
            handler: function(route) {
                this.redirect = route.query && route.query.redirect
            },
            immediate: true
        }
    },
    methods: {
        showPwd() {
            if (this.passwordType === 'password') {
                this.passwordType = ''
            } else {
                this.passwordType = 'password'
            }
            this.$nextTick(() => {
                this.$refs.password.focus()
            })
        },
        handleLogin() {
            this.$refs.loginForm.validate(valid => {
                if (valid) {
                    this.loading = true
                    this.$store
                        .dispatch('Home/login', this.loginForm)
                        .then(response => {
                            console.log(response.msg)
                            this.$router.push({ path: this.redirect || '/' })
                            this.loading = false
                            // this.changeAuthCode()
                        })
                        .catch(error => {
                            console.log(error)
                            this.loading = false
                            this.changeAuthCode()
                        })
                } else {
                    console.log('error submit!!')
                    return false
                }
            })
        },
        changeAuthCode() {
            this.authCodeSrc = this.$conf.host + 'authcode?' + Math.random()
        }
    }
}
</script>

<style lang="scss">
/* 修复input 背景不协调 和光标变色 */
/* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

$bg: #fff;
$light_gray: #000;
$cursor: #000;

@supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
    .login-container .el-input input {
        color: $cursor;
    }
}

/* reset element-ui css */
.login-container {
    .el-input {
        display: inline-block;
        height: 47px;
        width: 85%;

        input {
            background: transparent;
            border: 0px;
            -webkit-appearance: none;
            border-radius: 0px;
            padding: 12px 5px 12px 15px;
            color: $light_gray;
            height: 47px;
            caret-color: $cursor;

            &:-webkit-autofill {
                box-shadow: 0 0 0px 1000px $bg inset !important;
                -webkit-text-fill-color: $cursor !important;
            }
        }
    }

    .el-form-item {
        border: 1px solid rgba(255, 255, 255, 0.1);
        background: rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        color: #454545;
    }
}
</style>

<style lang="scss" scoped>
$bg: #fff;
$dark_gray: #889aa4;
$light_gray: #000;

.login-container {
    min-height: 100%;
    width: 100%;
    background-color: $bg;
    overflow: hidden;

    .login-form {
        position: relative;
        width: 520px;
        max-width: 100%;
        padding: 160px 35px 0;
        margin: 0 auto;
        overflow: hidden;
    }

    // .tips {
    //   font-size: 14px;
    //   color: #000;
    //   margin-bottom: 10px;

    //   span {
    //     &:first-of-type {
    //       margin-right: 16px;
    //     }
    //   }
    // }

    .svg-container {
        padding: 6px 5px 6px 15px;
        color: $dark_gray;
        vertical-align: middle;
        width: 30px;
        display: inline-block;
    }

    .title-container {
        position: relative;

        .title {
            font-size: 26px;
            color: $light_gray;
            margin: 0px auto 40px auto;
            text-align: center;
            font-weight: bold;
        }
    }

    .show-pwd {
        position: absolute;
        right: 10px;
        top: 7px;
        font-size: 16px;
        color: $dark_gray;
        cursor: pointer;
        user-select: none;
    }
    .authcode {
        position: absolute;
        cursor: pointer;
        height: 96%;
        right: 0;
        top: 2%;
        border-radius: 10px;
        overflow: hidden;
    }
}
</style>



